@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url(https://fonts.googleapis.com/css?family=Oxygen);
@import url(https://fonts.googleapis.com/css?family=Oxygen);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    background: #f2f2f2;
    font-family: 'Open Sans', sans-serif;
}

.wrap {
    width: 50%;
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.scrape {
    width: 100%;
    position: relative;
    display: flex;  
}

.scrapeBar {
    width: 100%;
    border: 3px solid #9c95a0;
    border-right: none;
    padding: 5px;
    height: 50.2px;
    border-radius: 5px 0 0 5px;
    outline: none;
    color: #757575;
    font-size: 28px;
}

.scrapeBar:focus {
    color: #020202;
}

.scrapeBar:hover {
    color: #020202;
}

.scrapeBar:invalid {
    color: #f84d4d;
    box-shadow: none;
}

.scrapeButton {
    width: 70px;
    height:66px;
    border: 1px solid #9c95a0;
    background: #9c95a0;
    color: #fff;
    border-radius: 0 5px 5px 0;
    cursor: pointer;
    font-size: 30px;
    padding-top: 5px;
    padding-left: 6px;
}

.title {
    color: #9a8ea1;
    font-size: 90px;
    position: absolute;
    top: 37%;
    left: 50%;
    transform: translate(-50%, -50%);
    word-spacing: 20px;
    font-family: 'Oxygen', sans-serif;
}

body {
    background: #f2f2f2;
    font-family: 'Oxygen', sans-serif;
}

.listTitle {
    color: #9a8ea1;
    font-size: 70px;
    position: absolute;
    padding-left: 30px;
}

.listBar {
    border-top: 2px solid #9a8ea1;
    background-color: #9a8ea1;
    border-color: #9a8ea1;
    position: relative;
    top: 150px;
    width: 30%;
    margin: 28px;
    height: 2px;
}

.list {
    width: 100%;
    list-style-type: none;
    position: relative;
    top: 150px;
}

.listItem {
    font-size: 33px;
    line-height: 90px;
    padding-left: 30px;
    padding-top: 2px;
}

.url {
    color: #9c84a8;
    text-decoration: none;
}

.error {
    width: 50%;
    position: absolute;
    top: 45%;
    left: 53%;
    transform: translate(-50%, -50%);
    font-size: 80px;
    color: #9c84a8;
}

